
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import backTop from '@/components/BackTop/backTop'
export default{
data(){
return {
resObj: {
JZS:'',
JTGX:'',
}
}
},
mounted(){
this.initInfo();
},
methods:{
getCJMLX(CJMLX){
if(CJMLX=='0'){
return '未分'
}else if(CJMLX=='1'){
return '城镇'
}else if(CJMLX=='2'){
return '农村'
}

},
getString(info){//0：无； 1：单设； 2：室内； 3：室外；
if(info=='0'){
return '无'
}else if(info=='1'){
return '单设'
}else if(info=='2'){
return '室内'
}else if(info=='3'){
return '室外'
}
return typeof info !='undefined' ? info : ''
},
initInfo(){
this.$vux.loading.show({
text: '加载中...'
})

this.$ajax.post('',{
'MSH.1':'healthFileBasicInfoService',
'MSH.2':'getById',
'CHZBM':this.$root.loginInfo.CHZBM,
'GRDAID':this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$vux.loading.hide()
if(typeof data !='undefined'){
console.log("----"+JSON.stringify(data))
this.resObj = data;
}
}).catch(error => {
console.log(error);
});

},

getJZLXBM(JZLXBM){
if(JZLXBM=='1'){
return '常驻'
}else if(JZLXBM=='2'){
return '暂住'
}else if(JZLXBM=='3'){
return '流动人口'
}else if(JZLXBM=='9'){
return '其他'
}else{
return ''
}

}

},



components: {
XHeader,Collapse,backTop
}
}






